.box {
  border: solid 1px #ccc;
  text-align: center;
  width: 170px;
  margin: 6px;
  box-shadow: 0 0 6px #ddd;
  padding: 4px;
  cursor: pointer;
  transition: all 0.3s ease;
  .img {
    width: 100px;
    height: 140px;
  }

  &:hover {
    opacity: 0.9;
    transform: rotate(1deg) scale(1.03);
  }
}
